export function cacl3xZutai(arr) {
  if (!Array.isArray(arr)) {
    throw new Error('');
  }
  if (arr.length !== 3) {
    throw new Error('3');
  }
  arr = arr.map(v => Number(v));
  const obj = {
    1: '',
    2: '',
    3: ''
  };
  const deduplicationArr = [...new Set(arr)]; //
  return obj[deduplicationArr.length];
}

/**
 * 
 * 
 * @param {} arr 
 */
export function calcKuadu(arr) {
  if (!Array.isArray(arr)) {
    throw new Error('');
  }
  arr = arr.map(v => Number(v));
  const max = Math.max(...arr);
  const min = Math.min(...arr);
  return max - min;
}

/**
 * 
 * 
 * @param {} arr 
 */
export function calcHezhi(arr) {
  if (!Array.isArray(arr)) {
    throw new Error('');
  }
  arr = arr.map(v => Number(v));
  return arr.reduce((a, b) => a + b);
}

/**
 * 
 * 
 * @param {Array} arr 
 * @returns Boolean
 */
export function calc2xDuizi(arr) {
  if (!Array.isArray(arr)) {
    throw new Error('');
  }
  if (arr.length !== 2) {
    throw new Error('2');
  }
  arr = arr.map(v => Number(v));
  const length = [...new Set(arr)].length;
  if (length === 1) return true;
  return false;
}
